<template>
  <div class="setting-root">
    <div class="balloon-box balloon-box-center">
      <span>Web - </span>
      <span>
        <img :src="getIconUrl()" alt="" width="64" height="64">
      </span>
      <span>&copy;2020 watagashi</span>
    </div>
    <div class="balloon-box">
      <span class="balloon-box-title"></span>
      <div class="balloon-box-element">
        <span class="balloon-box-element-message">
          
        </span>
        <button-s @click-event="ldsdShow"></button-s>
      </div>
      <div class="balloon-box-element">
        <span class="balloon-box-element-message">
          
        </span>
        <button-s @click-event="cacheDelete"></button-s>
      </div>
    </div>
    <div class="balloon-box">
      <span class="balloon-box-title"></span>
      <button-s @click-event="goToLicense"></button-s>
    </div>
    <check-dialog :click-yes="checkYes" :click-no="checkNo"
                  v-if="checkFlag">
      <template v-slot:title>
        
      </template>
      <template v-slot:message>
        
      </template>
    </check-dialog>
    <router-view name="ldsd" />
    <router-view name="license" />
  </div>
</template>

<script>
  import ButtonS from '../components/Button-S'
  import CheckDialog from '../components/CheckDialog'

  export default {
    name: 'SettingPage',
    components: {
      'button-s': ButtonS,
      'check-dialog': CheckDialog
    },
    data () {
      return {
        checkFlag: false
      }
    },
    methods: {
      ldsdShow () {
        this.$router.push({name: 'ldsd'})
      },
      cacheDelete () {
        this.checkFlag = true
      },
      checkYes () {
        localStorage.clear()
        this.$router.push({name: 'home'})
        document.location.reload()
      },
      checkNo () {
        this.checkFlag = false
      },
      goToLicense () {
        this.$router.push({name: 'license'})
      },
      getIconUrl () {
        return (process.env.NODE_ENV === 'development'
          ? ''
          : '/SotugyoSeisaku-lessonboard') +
          '/static/img/icons/app-icon.png'
      }
    }
  }
</script>

<style lang="scss" scoped>
@import "../assets/sass/baloon";

  .setting-root{
    max-width: 500px;
    margin: 0 auto;
  }
</style>



